import LevelBase from "./levelBase";
import { Exit, Player } from "../objects";

class Level10 extends LevelBase {
  constructor() {
    super();
    this.name = "The typhoon";
    this.diamondsGoal = 10;
    // this.dwarfRun = false
  }

  get id() {
    return this.constructor.name;
  }

  onInteractWith(el) {
    super.onInteractWith(el)
    
    if (el.isClass(Exit)) {
      this.onComplete();
    }
  }

  onAddElement(el) {
    if (el.constructor.name === Player.name) {
      el.power = 1;
      el.hasPickaxe = true;
    }

  }

}

Level10.key = 'level_10'
export default Level10;
