import Phaser from "phaser";
import LevelBase from "./levelBase";
import { Exit, Interactive, Player } from "../objects";
import { PIXEL_NAME, SCALE, TILE_SIZE } from "../../utils/constants";

class LevelMaze1 extends LevelBase {
  constructor() {
    super();
    this.name = "The Maze";
    this.isMaze = true;
    this.exitEye = null
  }

  connectToScene(scene) {
    super.connectToScene(scene);
    //  Enable lights and set a dark ambient color
    scene.lights.enable().setAmbientColor(0x000000);

    //  Our spotlight. 100px radius and white in color.
    this.light = scene.lights
      .addLight(this.player.alien.x, this.player.alien.y, 1000)
      .setColor(0xeaeaea)
      .setIntensity(1.5);
    scene.time.addEvent({
      delay: 100,
      callback: function () {
        this.light.x = this.player.alien.x;
        this.light.y = this.player.alien.y;
        Phaser.Geom.Ellipse.Random(this.player.alien, this.light);
      },
      callbackScope: this,
      repeat: -1,
    });

    scene.children.getByName("layer_1").setPipeline("Light2D");
    scene.children.getByName("layer_2").setPipeline("Light2D");
    scene.children.getByName("layer_1z2").setPipeline("Light2D");
    this.moveables.forEach((m) => m.alien.setPipeline("Light2D"));


    const exit = Object.values(this.interactiveElements).find(el => el.isClass(Exit))
    this.moveObject(this.player, {x: exit.position.x-3, y: exit.position.y+2});

    const preExit = new Interactive(PIXEL_NAME.INTERACTION, this)
    preExit.order = -1
    this.interactiveElements[`${exit.position.x-2}-${exit.position.y}`] = preExit
    preExit.setPosition({ x: exit.position.x-2, y: exit.position.y });


    this.scene.events.on('player_moves',  this.updateLight.bind(this))
  }

  updateLight(nextPos) {
    const exit = Object.values(this.interactiveElements).find(el => el.isClass(Exit))
    if(!exit) return false
    if(nextPos.x === exit.position.x-3 && nextPos.y === exit.position.y) {
      this.light.setRadius(500)
      this.light.setIntensity(1.3)

      // remove alpha tween
      if (this.eyeAnimation) {
        this.exitEye.alpha = 0
        this.eyeAnimation.remove()
      }
    }else if(nextPos.x === exit.position.x-2 && nextPos.y === exit.position.y) {
      // this.light.setIntensity(1.3)
      this.light.setRadius(250)
      this.light.setIntensity(0.8)

      // alpha tween from 0 to 0.5
      if (this.eyeAnimation) {
        this.eyeAnimation.remove()
      }
      this.exitEye.alpha = 0.1
      this.eyeAnimation = this.scene.tweens.add({
        targets: this.exitEye,
        alpha: 0.2,
        duration: 100,
        yoyo: true,
        repeat: -1,
      })
    }else if(nextPos.x === exit.position.x-1 && nextPos.y === exit.position.y) {
      // this.light.setIntensity(1.3)
      this.light.setIntensity(0.3)
      this.light.setRadius(125)

      // alpha tween from 0.5 to 1
      if (this.eyeAnimation) {
        this.eyeAnimation.remove()
      }
      this.exitEye.alpha = 0.3
      this.eyeAnimation = this.scene.tweens.add({
        targets: this.exitEye,
        alpha: 0.5,
        duration: 100,
        yoyo: true,
        repeat: -1,
      })
      
    }else if(nextPos.x === exit.position.x && nextPos.y === exit.position.y) {
      this.light.setIntensity(0.1)
    } else if(this.light !== 1.5) {
      this.light.setIntensity(1.5)
      this.light.setRadius(1000)
    }
    // const layer = this.scene.children.getByName('layer_2')
    // if(!layer) {
    //   return true
    // }
    // const map = layer.tilemap
    // let origin = this.player.position
    // this.moveables.forEach(el => {
    //   var dist = Phaser.Math.Distance.Snake(
    //       origin.x,
    //       origin.y,
    //       el.position.x,
    //       el.position.y
    //   );

    //   el.alien?.setAlpha(1 - 0.1 * dist);
    // })
    // layer.forEachTile((tile) => {
    //   var dist = Phaser.Math.Distance.Snake(
    //       origin.x,
    //       origin.y,
    //       tile.x,
    //       tile.y
    //   );

    //   // tile.setAlpha(0.5);
    //   tile.setAlpha(1 - 0.1 * dist);
    // });

  }

  get id() {
    return this.constructor.name;
  }

  onInteractWith(el) {
    super.onInteractWith(el);
    if (el.order === 0) {
      this.onComplete();
    }
  }

  onAddElement(el) {
    if (el.isClass(Player)) {
      el.power = 1;
      el.hasPickaxe = false;
      el.ligthTorch(); 
    }

    if (el.isClass(Exit)) {
      const layer = this.scene.children.getByName('layer_2')
      const map = layer.tilemap

      this.exitEye = this.scene.add
        .sprite(48, 16, "spark")
        .setOrigin(0.5, 0.5)
      this.exitEye.setTintFill(0xD25400)
      this.exitEye.setScale(layer.scaleX * 2, layer.scaleX * 4);
      this.exitEye.x = map.tileToWorldX(el.position.x-0.5);
      this.exitEye.y = map.tileToWorldY(el.position.y-0.4);
      this.exitEye.depth = 100
      this.exitEye.alpha = 0
    }
  }
}

LevelMaze1.key = 'maze_1'
export default LevelMaze1;
