import LevelBase from "./levelBase";
import { Exit, Player } from "../objects";

class Level11 extends LevelBase {
  constructor() {
    super();
    this.name = "The castle";
    this.diamondsGoal = 10;
    // this.dwarfRun = false
  }

  get id() {
    return this.constructor.name;
  }

  onInteractWith(el) {
    super.onInteractWith(el)
    
    if (el.isClass(Exit)) {
      this.onComplete();
    }
  }

  onAddElement(el) {
    if (el.constructor.name === Player.name) {
      el.power = 1;
      el.hasPickaxe = true;
    }

  }

}

Level11.key = 'level_11'
export default Level11;
