import Character from "./Character"
import Enemy from "./Enemy"
import Boulder from "./Boulder"
import { DIRS } from "../../utils/constants"
import Player from "./Player"

export default class NPCharacter extends Character {
  constructor(id, level, name) {
    super(id, level)
    this.name = name
    this.moveIntentDir = ""
    this.isFollowing = false
    this.isLeading = false
    this.leadingPath = [{ x: 1, y: 7 }, { x: 1, y: 8 }, { x: 1, y: 9 }, { x: 1, y: 10 }, { x: 1, y: 11 }, { x: 1, y: 12 }
      , { x: 2, y: 12 }, { x: 3, y: 12 }, { x: 3, y: 13 }, { x: 3, y: 14 }, { x: 3, y: 15 }, { x: 3, y: 16 }, { x: 3, y: 17 }, { x: 3, y: 18 }
      , { x: 4, y: 18 }, { x: 5, y: 18 }, { x: 5, y: 17 }, { x: 5, y: 16 }, { x: 5, y: 15 }, { x: 5, y: 14 }
      , { x: 6, y: 14 }, { x: 7, y: 14 }, { x: 8, y: 14 }, { x: 9, y: 14 }, { x: 10, y: 14 }, { x: 11, y: 14 }, { x: 12, y: 14 }, { x: 13, y: 14 }
      , { x: 14, y: 14 }, { x: 15, y: 14 }, { x: 16, y: 14 }, { x: 17, y: 14 }, { x: 18, y: 14 }, { x: 19, y: 14 }, { x: 20, y: 14 }, { x: 21, y: 14 }]
    level.scene.events.on('player_moves',  this.onPlayerMoves.bind(this))
  }
  
  onPlayerMoves(nextPos) {
    if(this.isCloseEnough(nextPos) && this.isLeading) {
      if(this.leadingPath.length) {
        const nextPos = this.leadingPath.shift()
        this.calcMoveIntentForLeader(nextPos)
        this.level.moveObject(this, nextPos)
      }
    }
  }

  isCloseEnough(pos) {
    return (
      // NORTH
      (this.position.x === pos.x && this.position.y - 1 === pos.y) ||
      // EAST
      (this.position.x + 1 === pos.x && this.position.y === pos.y) ||
      // SOUTH
      (this.position.x === pos.x && this.position.y + 1 === pos.y) ||
      // WEST
      (this.position.x - 1 === pos.x && this.position.y === pos.y)
    )
  }
  calcMoveIntentForLeader(nextPos) {
    this.moveIntentDir = this.position.y < nextPos.y ? DIRS.DOWN : this.moveIntentDir
    this.moveIntentDir = this.position.y > nextPos.y ? DIRS.UP : this.moveIntentDir
    if (this.position.x < nextPos.x) {
      this.moveIntentDir = DIRS.LEFT
      this.animRunRight()
    }
    if (this.position.x > nextPos.x) {
      this.moveIntentDir = DIRS.RIGHT
      this.animRunLeft()
    }

    // this.moveObject(follower, followerNextPos)

    // if(this.moveIntentDir === DIRS.UP) {
    //   this.alien.depth = 20
    //   player.alien.depth = 0
    // } else {
    //   player.alien.depth = 20
    //   this.alien.depth = 0
    // }
  }

  destroy() {
    // animate die

  }
  animIdle() {
    this.alien?.play(`${this.name}_idle`)
  }

  animRun() {
    let animRunVariant = ""
    if (this.moveIntentDir === DIRS.UP) {
      animRunVariant = "_up"
    }
    if (this.moveIntentDir === DIRS.DOWN) {
      animRunVariant = "_down"
    }
    const animName = `${this.name}_run${animRunVariant}`
    if (this.alien.anims.currentAnim.key !== animName) {
      this.alien.play(animName)
    }
  }
  animRunLeft(){
    this.alien.setFlipX(true)
    this.alien.setOrigin(0.25, 0.25)
  }
  animRunRight(){
    this.alien.setFlipX(false)
    this.alien.setOrigin(0, 0.25)
  }

  die() {
    this.inactive = true
    this.alien.alpha = 0
    this.level.scene.events.off('player_moves',  this.onPlayerMoves)
    this.destroy()
    // this.level.onCharacterDie(this)
  }

  overrunBy(el) {
    if (el.constructor.className === Boulder.className) {
      this.level.scene.events.emit("sounds:death_by_rock_hit")
    }
    if (el.constructor.className !== Player.className) {
      this.die()
    }
  }
  runningOver(el) {
    if (el.constructor.className === Enemy.className) {
      this.die()
    }
    // this.isBrakingGround = el.constructor.className === Ground.className
  }
}

NPCharacter.className = "NPCharacter"
